import uCharts from '@/components/u-charts/u-charts.js';
import  { isJSON } from '@/common/checker.js';
var _self;
var canvaColumn=null;

export default {
	data() {
		return {
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			textarea:''
		}
	},
	onLoad() {
		_self = this;











		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData(){
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data:{
				},
				success: function(res) {
					console.log(res.data.data)
					let ColumnMeter={categories:[],series:[]};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					ColumnMeter.categories=res.data.data.ColumnMeter.categories;
					//这里的series数据,如果为Meter，series[0]定义为外层数据，series[1]定义为内层数据
					ColumnMeter.series=res.data.data.ColumnMeter.series;
					_self.textarea = JSON.stringify(res.data.data.ColumnMeter);
					_self.showColumnMeter("canvasColumnMeter",ColumnMeter);
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		showColumnMeter(canvasId,chartData){
			canvaColumn=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'column',
				padding:[15,15,0,15],
				legend:{
					show:true,
					padding:5,
					lineHeight:11,
					margin:0,
				},
				fontSize:11,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				animation: true,
				rotate:false,
				categories: chartData.categories,
				series: chartData.series,
				xAxis: {
					disableGrid:true,
				},
				yAxis: {
					//disabled:true
				},
				dataLabel: true,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					column: {
						type:'meter',
						width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length,
						meter:{
							//这个是外层边框的宽度
							border:4,
							//这个是内部填充颜色
							fillColor:'#E5FDC3'
						}
					}
				  }
			});
			
		},
		touchColumn(e){
			canvaColumn.showToolTip(e, {
				format: function (item, category) {
					if(typeof item.data === 'object'){
						return category + ' ' + item.name + ':' + item.data.value 
					}else{
						return category + ' ' + item.name + ':' + item.data 
					}
				}
			});
		},
		changeData(){
			if(isJSON(_self.textarea)){
				let newdata=JSON.parse(_self.textarea);
				canvaColumn.updateData({
					series: newdata.series,
					categories: newdata.categories
				});
			}else{
				uni.showToast({
					title:'数据格式错误',
					image:'../../../static/images/alert-warning.png'
				})
			}
		}
	}
}
